html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

html {
    font-size: 62.5%;
    height: 100vh;
    background: lightgrey;
}

#h1-must-register {
  text-align: center;
  font-size: 4.8rem;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  margin-bottom: 1.5rem;
}

.register-field {
  margin: 0 auto;
  display: flex;
  justify-content: center;
}

.labels-register-form {
  font-size: 1.8rem;
  margin-right: 1rem
}

#button-container-register {
  display: flex;
  width: 30rem;
  margin: 2rem auto;
}

#button-register {
  color: black;
  display: block;
  margin: 0 auto;
  font-size: 1.8rem;
  cursor: pointer;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}

#button-register:hover {
  color: white;
  background: black;
  transition: 0.25s;
}

#comment-error-message {
  text-align: center;
  color: red;
  font-size: 1.8rem;
  margin-top: 0.5rem;
}

#comment-upload-message {
  text-align: center;
  color: black;
  font-size: 1.8rem;
  margin-top: 0.5rem;
}

#h1-register-thanks {
  text-align: center;
  font-weight: bold;
  font-size: 4.8rem;
}